body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}
.results__card {
  min-height: 380px;
  margin: 30px 8px 10px;
  text-align: center;
}

.results__header {
  min-height: 40px;
  padding: 10px !important;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.results__header:hover {
  display: block;
  overflow: visible;
  text-overflow: unset;
  white-space: normal;
}

.results__header a {
  text-decoration: none;
  cursor: pointer;
  color: #0069d9;
}

.results__header a:hover {
  text-decoration: none;
  color: #000;
}

.results__img {
  width: 120px;
}

.results__button {
  margin: 20px auto 0;
}

.results__badge {
  margin-left: 10px;
}

.badge {
  font-size: 80% !important;
}

.results__pagination {
  margin: 20px auto 0;
}

@media (max-width: 576px) {
  .results__card {
    min-height: 350px;
  }
}
.spinner__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 100vh;
}

.spinner__custom {
  margin-right: 20px;
}
.details__card {
  width: 100%;
  margin: 20px 0;
}

.badge__details {
  margin-right: 10px;
}


.navbar__custom__col {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

@media (max-width: 991px) {
  .navbar__custom button {
    margin-top: 15px;
  }
}

@media (max-width: 425px) {
  .navbar__custom h1 {
    font-size: 30px;
  }
}

.navbar__logo {
  margin-right: 10px;
}
